<template>
  <div class="zhibei" :style="{'transform': `rotateZ(${angle}deg)`}" @click="toggleLock()"></div>
</template>

<script>
import * as util from "../assets/util";

export default {
  name: "compass",
  data() {
    return {
      locked: false,
      angle: 0
    };
  },
  watch: {
    angle: function() {
      this.$emit("offset", this.angle);
    }
  },
  methods: {
    toggleLock: function(){
      if(this.locked){
        this.locked = false;
      }else{
        this.locked = true;
        this.angle = 0;
        this.$emit("offset", this.angle);
      }
      this.$createToast({
        type: 'txt',
        txt: this.locked ? "方向锁定" : "方向跟随"
      }).show()
    },
    deviceOrientationListener: function(event) {
      if (this.locked) {
        this.angle = 0;
      } else if(event.alpha){
        this.angle = event.alpha;
      }
    }
  },
  created: function() {
    if (window.DeviceOrientationEvent) {
      let vm = this;
      window.addEventListener(
        "deviceorientation",
        util.throttle(vm.deviceOrientationListener, 10, 30)
      );
    } else {
      alert("Sorry your browser doesn't support Device Orientation");
    }
  },
  mounted: function() {}
};
</script>

<style scoped>
.zhibei {
  position: fixed;
  left: 0.5em;
  top: 0.5em;
  width: 3em;
  height: 3em;
  background: url(../assets/img/zhibei.png) center no-repeat
    rgba(255, 255, 255, 0.7);
  background-size: contain;
  z-index: 9;
  border: 1px solid #f5f5f5;
  box-shadow: 1px 1px 1px #eee;
  border-radius: 50%;
  overflow: hidden;
}
</style>
